 .wptravel-block-trip-search{
	.wp-travel-search form {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
		-moz-column-gap: 1rem;
		column-gap: 1rem;
		align-items: end;
		justify-items: center;
		select, input{
			width: 100%;
			height: 30px;
		}
		input.button{
			height: 50px;
			padding: 0px 20px;
		}
	}
}

#wptravel-block-trip-search .button:focus,
#wptravel-block-trip-search .button:hover{
	color: #fff;
	background-color: #000;
	border-color: #000;
}



#wptravel-block-trip-search .wp-travel-search p {
    margin-bottom: 0em;
}